import { Box, Group, TextInput, Button } from '@mantine/core'
import { useForm } from '@mantine/form'
import 'dayjs/locale/ja'
import { DatePicker } from '@mantine/dates'

const NewCamp = (props: any) => {
   const form = useForm({
      initialValues: {
         name: '',
         description: '',
         location: '',
         start_date: '',
         end_date: '',
      },

      validate: {
         name: (value) => (value.length < 2 ? '' : null),
         start_date: (value) => (value.length !== undefined ? '' : null),
         end_date: (value) => (value.length !== undefined ? '' : null),
      },
   })

   return (
      <Box>
         <form onSubmit={form.onSubmit((values) => props.submit(values))}>
            <TextInput
               label=''
               placeholder=': '
               {...form.getInputProps('name')}
            />
            <TextInput label='' placeholder='' {...form.getInputProps('description')} />
            <TextInput label='' placeholder=': ' {...form.getInputProps('location')} />
            <DatePicker label='' placeholder='Pick date' locale='ja' {...form.getInputProps('start_date')} />
            <DatePicker label='' placeholder='Pick date' locale='ja' {...form.getInputProps('end_date')} />
            <Group position='right' mt='md'>
               <Button type='submit'></Button>
            </Group>
         </form>
      </Box>
   )
}

export default NewCamp
